<template>
  <section class="pc-container">
    <div class="pcoded-content">
      <div class="page-header">
        <div class="page-block">
          <div class="row align-items-center">
            <div class="col-md-6">
              <div class="page-header-title">
                <h5 class="m-b-10">Expedientes</h5>
              </div>
              <ul class="breadcrumb">
                <li class="breadcrumb-item">
                  <router-link to="/sisge">Inicio</router-link>
                </li>
                <li class="breadcrumb-item">Visualización de expedientes</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="miTabla">
                  <thead>
                    <tr>
                      <th>N° de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Demandante</th>
                      <th>Demandado</th>
                      <th>Pretensión</th>
                      <th>Materia</th>
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="expediente in expedientes" :key="expediente.exp_id">
                      <td >
                        <router-link :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                          {{ expediente.numero }}
                        </router-link>
                      </td>
                      <td>{{ expediente.fecha_inicio }}</td>
                      <td class="col-md-1" v-if="expediente.procesal === 'demandante'">{{ getPersonaName(expediente) }}
                      </td>
                      <td>Unprg</td>
                      <td v-if="expediente.procesal === 'demandado'">{{ getPersonaName(expediente) }}</td>
                      <td>{{ expediente.pretencion }}</td>
                      <td>{{ expediente.materia }}</td>
                      <td>
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
  name: 'Buscar',
  props: ['tipo_exp'],
  async created() {
    await this.listarExpedientes();
  },

  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
  },

  data() {
    return {
      expedientes: [],
      dataTable: null,
      tipo_expediente: null
    };
  },

  computed: {
    tipoExpediente() {
      return this.$route.params.tipo_exp
    }
  },

  methods: {
    ...mapActions(['get', 'post']),

    async listarExpedientes() {
      let response; // Declarar la variable response antes del bloque try

      try {
        if (this.tipo_exp === "total") {
          response = await this.get({
            url: this.$store.getters.get__url + '/proceeding',
            token: this.$store.getters.get__token,
          });
        } else if (this.tipo_exp === "tramite") {
          response = await this.post({
            url: this.$store.getters.get__url + '/proceeding/listarestado',
            token: this.$store.getters.get__token,
            params: {
              exp_estado_proceso: "EN TRAMITE"
            }
          });
        } else if (this.tipo_exp === "ejecucion") {
          response = await this.post({
            url: this.$store.getters.get__url + '/proceeding/listarestado',
            token: this.$store.getters.get__token,
            params: {
              exp_estado_proceso: "EN EJECUCION"
            }
          });
        }

        this.expedientes = response.data;
        this.initDataTable();
      } catch (error) {
        console.error(error);
      }
    },

    async initDataTable() {
      if (this.dataTable) {
        this.dataTable.destroy();
      }

      this.$nextTick(() => {
        this.dataTable = $('#miTabla').DataTable({
          language: {
            url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
          },
        });
        feather.replace();
      });
    },

    badgeClass(estado) {
      return {
        'badge tramite p-2 rounded': estado === 'En Tramite',
        'badge ejecucion p-2 rounded': estado === 'En Ejecucion',
      };
    },

    getPersonaName(expediente) {
      if (expediente.tipo_persona === 'natural') {
        return `${expediente.apellido_paterno} ${expediente.apellido_materno} ${expediente.nombres}`;
      } else {
        return expediente.razon_social;
      }
    }
  },

  beforeDestroy() {
    if (this.dataTable) {
      this.dataTable.destroy();
    }
  },
};
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}

.tramite {
  background-color: #fff6e5;
  color: #F4BD50;
  border: 1px solid #F4BD50;
}

.ejecucion {
  background-color: #EAFEF3;
  color: #2ecc71;
  border: 1px solid #2ecc71;
}

.badge {
  font-size: 13px !important;
}
</style>
